* {
  margin: 0px;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  background-color: lightgray;
  height: 50vh;
}

.App-header {
  height: 400px;
  width: 350px;
  background-color: white;
  display: flex;
  flex-direction: column;
  padding: 15px;
  color: gray;
  transform: translate(20px, 20px);
  box-shadow: 5px 5px 10px gray;
}

.App-principal {
  display: flex;
  flex-direction: row;
}
.App-logo {
  height: 50px;
  width: 50px;
  pointer-events: none;
  border-radius: 50%;
}

.App-titles {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 15px;
}
.App-title {
  font-size: 16px;
}

.App-data {
  font-size: 14px;
}

.App-text {
  font-size: 14px;
  padding-top: 25px;
  text-align: left;
}

.App-end {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
}
.App-link {
  color: grey;
  text-decoration: none;
}

.fas {
  color: rgb(207, 33, 33);
  font-size: 24px;
}
